import React from "react"
import { graphql } from "gatsby"
import BackgroundImage from 'gatsby-background-image'
import Layout from "../components/layout/Layout";

export default function BlurbPage({ data }) {
	console.log(data);
	const post = data.markdownRemark;
	console.log(post.frontmatter.image);

	return (
		<Layout>
			<div>
				{
					post.frontmatter.image !== null && post.frontmatter.image !== undefined ?
					<BackgroundImage
						fluid={post.frontmatter.image.childImageSharp.gatsbyImageData}
						backgroundColor={`#040e18`}
						style={{
							backgroundPosition: 'top center',
							backgroundRepeat: 'none',
							backgroundSize: 'cover',
							minHeight: '430px',
							height: '70vh',
							marginBottom: 60,
							position: 'relative'
						}}
					>
						<div style={{ padding: 20, backgroundColor: 'rgba(224,224,224,0.85)', position: 'absolute', bottom: '10%', right: '5%', borderRadius: 15}}>
							<h1>{post.frontmatter.title}</h1>
							<p>{post.frontmatter.summary}</p>
						</div>
					</BackgroundImage>
					:
					<div className="box" style={{padding: 20}}>
						<div style={{ fontSize: '9pt', color: 'lightgrey' }}>{post.frontmatter.date}</div>
						<div className="titleText secondary">{post.frontmatter.title}</div>
						<div className="summaryText">{post.frontmatter.summary}</div>
					</div>
				}
				<div style={{marginTop: 25, color: 'lightgrey'}} dangerouslySetInnerHTML={{ __html: post.html }} />
			</div>
		</Layout>
	)
}

export const query = graphql`
  query($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      html
      frontmatter {
        title
        summary
        date(formatString: "DD MMMM, YYYY")
      }
    }
  }
`